import React from 'react';
import aa from "../img/tou.png"
import bb from "../img/xie.png"
import '../css/Fenlei.css';
import $ from 'jquery'

import { NavLink, HashRouter as Router, Route, Redirect, Switch } from 'react-router-dom'
//import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';

class Fenlei extends React.Component {
	constructor(props){
		super(props)
		this.state={
			list:[],
			arr:[],
			arr2:[],
			
		}
	}

	
	
	render() {
		return(
			<div className="title">
               <header className='heard-a'>
					<span className="iconfont">&#xe606;</span>
					<input type='text' placeholder="搜索你想要的商品"  />
				</header>

				<div className="ul1">
					{
						this.state.list.map((item,i)=>{
							return(
								<NavLink to={"/index/detail/"+item.pid}>
									<div className="ul1-1">
										<img src ={item.pimg} alt="d"/>
										<div className="ul1-2">
											<p className="p1">{item.pname}</p>
											<p className="p2">{item.pprice}</p>
										</div>
									</div>											
								</NavLink>
							)
						})
					}
				</div>
         </div>
		)
	}
	componentDidMount(e){
	
		var _this=this;
		$.ajax({
			type:'get',
			url:"http://jx.xuzhixiang.top/ap/api/productlist.php",
			data:{uid:1120},
			dataType:'json',
			success:function(data){
		
				_this.setState({list:data.data})
			}
		})
	}
}

export default Fenlei;


