import React from "react";
import { NavLink, HashRouter as Router, Route, Redirect, Switch } from 'react-router-dom';

import Home from "./Home";
import Fenlei from "./Fenlei";
import Cart from "./Cart";
import My from "./My";
import Detail from "./Detail";
import Regist from "./Regist"
import Login from "./Login"


class Index extends React.Component {
	render() {
		return(
			<div>
				<Router>
				  <div className='index'>
					<Switch>
						<Route path="/index/cart" component={Cart}></Route>
						<Route path="/index/home" component={Home}></Route>
						<Route path="/index/fenlei" component={Fenlei}></Route>
						<Route path="/index/my" component={My}></Route>
						<Route path="/index/detail/:id" component={Detail}></Route>
						<Route path="/index/regist" component={Regist}></Route>
              <Route path="/index/login" component={Login}></Route>
						
              <Redirect to="/index/regist"/>
						
						

					</Switch>
					
					<footer>
						<NavLink to="/index/home">
							<i className="iconfont">&#xe619;</i>
							<p>首页</p>
						</NavLink>
						<NavLink to="/index/fenlei">
							<i className="iconfont">&#xe603;</i>
							<p>分类</p>
						</NavLink>
						<NavLink to="/index/cart">
							<i className="iconfont">&#xe726;</i>
							<p>购物车</p>
						</NavLink>
						<NavLink to="/index/my">
							<i className="iconfont">&#xe600;</i>
							<p>我的</p>
						</NavLink>
						
					</footer>
					
				  </div>
					
				</Router>
				
			</div>
		)
	}
}
export default Index;