import React from 'react';
import $ from 'jquery'
import '../css/Home.css';
import { NavLink, HashRouter as Router, Route, Redirect, Switch } from 'react-router-dom'
import { Carousel, WingBlank } from 'antd-mobile';
import banner1 from "../img/banner1.jpg"
import banner2 from "../img/banner2.jpg"
import banner3 from "../img/banner3.jpg"
import banner4 from "../img/banner4.jpg"
import chao from "../img/chao.jpg"
import left from "../img/left.jpg"
import right from "../img/right.jpg"
import xiari from "../img/xia-a.jpg"
import tuoxie from "../img/tuoxie.jpg"
import mao from "../img/mao.jpg"
import haxi from "../img/haxi.png"
import bao from "../img/bao.png"




class Home extends React.Component {
	constructor(props) {
		super(props)
		this.state ={
			data: [banner1, banner2, banner3,banner4],
    		imgHeight: 176,
    		goods:[]
		}
	}
	componentDidMount() {
		
		var _this=this;
		$.ajax({
			type:"get",
			url:"http://jx.xuzhixiang.top/ap/api/productlist.php",
			async:true,
			data:{uid:1120},
			dataType:'json',
			success:function(data){
//				console.log(data)
				_this.setState({goods:data.data})

			}
		});
	}
	tap(){
	
		
	}
	aaa(){
		$(".aaa").hide();
		setTimeout(function(){
			$(".aaa").show().fadeIn();
		},2000)
	}
	render() {
		return(
			<div className='home'>
								
                <header>	
                	<span className="iconfont">&#xe606;</span>
                	<input type='text' placeholder="搜索你想要的商品"  />
					<p className="iconfont">&#xe65b;</p>				
                </header>

                <section>
                	<WingBlank className='banner'>
				        <Carousel
				          autoplay={true}
				          infinite
				          slideWidth={1.05}
				        >
				          {this.state.data.map((item,i) => (
				            <a
				              key={i}
				             
				              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
				            >
				              <img
				                src={item}
				                alt=""
				                style={{ width: '100%', height:'100%',verticalAlign: 'top' }}
				                onLoad={() => {
				                  // fire window resize event to change height
				                  window.dispatchEvent(new Event('resize'));
				                  this.setState({ imgHeight: 'auto' });
				                }}
				              />
				            </a>
				          ))}
				        </Carousel>
				    </WingBlank>
				    
			      	<div className="bir"></div>
			      	<div className='bir-a'>
			      		<p></p>
			      		<span>爱立撕专题活动</span>
			      		<p></p>			      		
			      	</div>
			     
			      	<div className='chao' onClick={this.tap.bind(this)}>
			      		<span><img src={chao} alt={chao}/></span>
			      	</div>
			    
			      
			      	<div className='xin'>
			      		<span><img src={left} alt={left}/></span>
			      		<span><img src={right} alt={right}/></span>
			      	</div>
			      
			      
			      	<div className="bir"></div>
			      	<div className='bir-a'>
			      		<p></p>
			      		<span>下滑有彩蛋</span>
			      		<p></p>

			      	</div>
			      
			      	<div className='chao'>
			      		<span><img src={haxi} alt={haxi}/></span>
			      	</div>
			      	<div className="bir"></div>
			      	
			      	<div className='bir-a'>
			      		
			      		<span>玩转夏日风之单品推荐</span>
			      		

			      	</div>
			      	
			      	<div className='lunbo'>
			      		<div className='top-a'>
			      			
			      		{
			      			this.state.goods.map((item,i)=>{
			      				return(
			      					
							      		<div key={i}>
							      			<NavLink to={'/index/detail/'+item.pid}>
							      			<span>
							      				<p>{item.pname}</p>
							      				<p>￥{item.pprice}</p>
							      			</span>
							      			<span>
							      				<img src={item.pimg} alt={tuoxie} />
							      			</span>
											</NavLink>
							      		</div>
							      	
			      				)
			      			})
			      		}
			      			
			      		</div>	
			      	
			      	
				     
				      	
			      	</div>
			      	
			      	
			      	<div className='bir-a'>
			      		<p></p>
			      		<span>下滑更精彩</span>
			      		<p></p>

			      	</div>
			      	
			      	<div className='chao'>
			      		<span><img src={bao} alt={bao}/></span>
			      	</div>
			      	
			      	<div className="bir"></div>
			      	
			      	<div className='bir-a'>
			      		
			      		<span>已到底部</span>
			      		

			      	</div>
			      	
                </section>
                
            </div>
		)

		
	}
}

export default Home;
