
import React from "react";
import "../css/Detail.css";


import img3 from "../img/8.jpg";
import $ from 'jquery';
import {NavLink,HashRouter as Router,Route,Redirect,Switch} from 'react-router-dom';

//import { Carousel, WingBlank } from 'antd-mobile';

class Detail extends React.Component{
  
      constructor(props){
		super(props)
		this.state={
            detail:{},
            num:"4",
		}
	}

    fanhui(e){
        // this.props.history.push(/)
        this.props.history.go(-1)
    }
    
    add(){
		$(".beijing").css({"display":"block"})
		var timer = setInterval(function(){
			clearInterval(timer)
			$(".beijing").css({"display":"none"})
		},2000)
		var _this=this;
		$.ajax({
			type:"get",
			url:"http://jx.xuzhixiang.top/ap/api/add-product.php",
			data:{uid:1120,pid:_this.props.match.params.id,pnum:1},
			dataType:"json",
			async:true,
			success:function(data){
//              console.log(data)
          
			}
		});
		
    }
    
    quxiao(){
        $(".div13").hide()
    }
   
    render(){
        return(
            <div>
                 <div className="div-a">
                    <span className="iconfont span1" onClick={this.fanhui.bind(this)}>&#xe646;</span>
                    <span className="span2">商品</span>
                </div>

               	<div className="div-b">
               		<img src={this.state.detail.pimg} onClick={this.quxiao.bind(this)}/>
					<p className="p1">{this.state.detail.pdesc}</p>
					<p className="p2">{this.state.detail.pprice}</p>
					
               	</div>
                   <div className="div12">
                        
               	        <button onClick={this.add.bind(this)} className="btn1">加入购物车</button>
                           <button>立即购买</button>
                   </div>
                  <div className="beijing">
				  	<img src={img3} />
				  </div>
            </div>
        )
    }
    
    
    componentDidMount() {
          		// console.log(this.props.match.params.id)
			var _this=this;
			$.ajax({
				type:'get',
				url:"http://jx.xuzhixiang.top/ap/api/detail.php",
				data:{uid:1120,id:_this.props.match.params.id,pnum:1},
				dataType:'json',
				async:true,
				success:function(data){
//					console.log(data)
					_this.setState({detail:data.data})
				}
			});
      }
    
    
}
export default Detail;

