import React from 'react';
import { NavBar, Icon,List, Stepper } from 'antd-mobile';
import img2 from "../img/lunbo2.jpg";

import '../css/Cart.css'
import $ from 'jquery';

import {NavLink,HashRouter as Router,Route,Link,Redirect,Switch} from 'react-router-dom'


class Cart extends React.Component{
	constructor(props){
		super(props)
		this.state = {
		    arr:[],
			checkall:false
		    
	    };
	}
	/*删除按钮*/
	sc(id,i){
		var _this=this
		$.ajax({
			type:"get",
			url:"http://jx.xuzhixiang.top/ap/api/cart-delete.php",
			async:true,
			dataType:'json',
			data:{uid:1120,pid:id},
			success:function(data){
				var arr1=_this.state.arr;
				arr1.splice(i,1);
				_this.setState({arr:arr1})
				
			}
		});
	}
	/*加减数量*/
	onChange(id,i,value){
		var _this=this;
		$.ajax({
			type:"get",
			url:"http://jx.xuzhixiang.top/ap/api/cart-update-num.php",
			async:true,
			data:{uid:1120,pid:id,pnum:value},
			dataType:'json',
			success:function(data){
				//console.log(data)
				var arr1=_this.state.arr;
				arr1[i].pnum=value;
				_this.setState({arr:arr1})
			}
		});
	}
	
	/*总价*/
	zong(){
		var zong=0;
		this.state.arr.map((item,i)=>{
			if(item.selected==true){
				zong+=item.pprice*item.pnum
			}
			
		})
		//console.log('aa')
		return zong;
	}
	//全选
		checkchange(){
//		console.log(this.state.checkall)
		if(this.state.checkall){
			this.setState({checkall:false})
		}else{
			this.setState({checkall:true})
		}
		var _this=this;
		this.state.arr.map((item,i)=>{
			item.selected=!_this.state.checkall;
		})
	}
		//单选
	changeitem(i){
		var list1=this.state.arr;
		list1[i].selected=!list1[i].selected
		this.setState({arr:list1},function(){
			var sel=this.state.arr.filter(function(item){
			return item.selected==true;
		})
		
		sel.length==this.state.arr.length?this.setState({checkall:true}):this.setState({checkall:false})
		})
		
		
	}
	render(){
    	var _this=this;
        return(
            <div className="cart">
                <header className='header-c'>购物车</header>
{/*获取的数据*/}				
				<section className="cartBox">
					<ul className="xqbox">
						{
							this.state.arr.map((item,i)=>{
								return(
									<li key={i}>
											
										
										<NavLink to={"/index/cart/"+item.pid}>
										<p className="del"><div className="iconfont" onClick={this.sc.bind(this,item.pid,i)}>&#xe61e;</div></p>
										<img src={item.pimg}/>
										</NavLink>
										<div className="num">
								<input type='checkbox' checked={item.selected} onChange={this.changeitem.bind(this,i)}/>

											<p>{item.pname}</p>
											<p>¥{item.pprice}</p>
											
											<p>
												<List.Item
										          wrap
										          extra={
										            <Stepper
										              style={{ width: '100%', minWidth: '100px' }}
										              showNumber
										              min={1}
										              value={item.pnum}
										              onChange={this.onChange.bind(this,item.pid,i)}
										            />}
										        ></List.Item>
											</p>
											
										</div>
										
										
								        
									</li>
								)
							})
						}
					</ul>
				</section>
{/*总数*/}
				<div className="jies">

					
					<div>
				<span>全选:<input type='checkbox' checked={this.state.checkall} onChange={this.checkchange.bind(this)}/></span>
					
						<p className="xuan">合计:¥{this.zong.bind(this)()}</p>
						<p>不含运费</p>
					</div>
					<button>去结算</button>
				</div>

            </div>
        )
    }
    componentDidMount(){
		var _this=this;
		$.ajax({
	    		type:"get",
	    		url:"http://jx.xuzhixiang.top/ap/api/cart-list.php",
	    		async:true,
	    		data:{id:1120},
	    		dataType:'json',
	    		success:function(data){
	    			_this.setState({arr:data.data});

	    		}
	    })
	}
}

var styleObj={
	background:'#44505e',
	color:'#fff'
}

export default Cart;