import React from 'react';

import {NavLink,HashRouter as Router,Route,Redirect,Switch} from 'react-router-dom'
import "../css/Login.css"
import img from "../img/1.jpg"
import $ from "jquery";


class Login extends React.Component{
    constructor(props){
        super(props)
        this.state={
          
            phone:"",
            username:"",
            password:"",
            
        }
    }
    zhang(){//账号
        
        this.setState({username:this.refs.word.value})
    }
    mi(){//密码
        this.setState({password:this.refs.mima.value})
    }

    regist(){
        var _this=this; 
           var obj={
            "name":this.state.username,
            "password":this.state.password
           }
    
        $.ajax({
          	
            type:'post',
            url:"http://39.96.93.180/alsapp-0.0.1-SNAPSHOT/login.do",
            data:obj,
            
            dataType:"json",
            async:true,
            success:function(data){
               console.log(data)
               if(data.code==1&_this.state.username!=""&&_this.state.password!=""){
               	 localStorage.setItem("code",JSON.stringify(data.code))
                console.log(data)
                alert("登录成功")
                
				_this.props.history.push('/index/my')              
            }
            }
        })
    
    }



    fanhui(e){
       
        this.props.history.go(-1)
    }
    render(){
        return(
            <div>
                <div className="div5">
                    <span className="iconfont span1" onClick={this.fanhui.bind(this)}>&#xe646;</span>
                    <span className="span2">登录</span>
                </div>
                <Router>
                    <div>
                       <header className="header2">
                           <span><img src={img}/></span>
                       </header>
                       <div className="div6">
                            <form>
                               
                                <div className="div1-1">
                                     <span className="iconfont font1">&#xe65c;</span>
                                    <input type="text" placeholder="  请设置账号"  onBlur={this.zhang.bind(this)} ref='word'></input>
                                </div>
                                <div className="div1-1">
                                     <span className="iconfont font1">&#xe612;</span>
                                    <input type="text" placeholder="  请设置密码" onBlur={this.mi.bind(this)} ref="mima"></input>
                                </div>
                                <div className="div6-r"  onClick={this.regist.bind(this)}>确定</div>
                                <div className="div6-1">
                                <span>忘记密码?</span>
                                </div>
                            </form>
                            <div className="div6-2">
                                第三方平台登录
                            </div>
                            <div className="div6-3">
                                <figure>
                                    <figcaption className="iconfont fon1">&#xe73e;</figcaption>
                                    <span>
                                        QQ登录
                                    </span>
                                </figure>
                                <figure>
                                    <figcaption className="iconfont fon2">&#xe73b;</figcaption>
                                    <span>
                                        微信登录
                                    </span>
                                </figure>
                                <figure>
                                    <figcaption className="iconfont fon3">&#xe636;</figcaption>
                                    <span>
                                        微博登录
                                    </span>
                                </figure>
                            </div>
                       </div>
                    </div>
                </Router>
            </div>
        )
    }
}

export default Login;