import React from 'react';

import {NavLink,HashRouter as Router,Route,Redirect,Switch} from 'react-router-dom'
import "../css/Regist.css";
import Login from "../components/Login";
import img from "../img/1.jpg";
import $ from "jquery";


class Regist extends React.Component{
    constructor(props){
        super(props)
        this.state={
            str:"shurukuang",
            str1:"text",
            phone:"",
            username:"",
            password:"",
            str2:"",
            arr:[]
            
        }
    }
// -----------------------------------------------
    phone(){//手机号
        // console.log(this.refs.pho.value)
        
        this.setState({phone:this.refs.pho.value})//实现状态与输入内容同步
    }
    zhang(){//账号
        this.setState({username:this.refs.word.value})
    }
    mi(){//密码
        this.setState({password:this.refs.mima.value})
    }



    regist(){
        var _this=this; 
           var obj={
            'userPhone':this.state.phone,
            'userName':this.state.username,
            "userPassword":this.state.password
           }
  
        $.ajax({
          
            type:'post',
            url:"http://39.96.93.180/alsapp-0.0.1-SNAPSHOT/register.do",
            data:obj,
            
            dataType:"json",
            async:true,
            success:function(data){
                
                if(data.code==1&&_this.state.phone!=""&&_this.state.username!=""&&_this.state.password!=""){
                    localStorage.setItem("obj",JSON.stringify(obj))
                    alert("注册成功")
                    
                    _this.props.history.push('/index/login')
                    
                }if(_this.state.phone==""||_this.state.username==""||_this.state.password==""){
                    _this.setState({"str2":"请输入完整信息"})
                }
               console.log(data)
               if(data.msg=="账号已存在"&&_this.state.username!=""){
                _this.setState({"str2":"账户已存在"})
            }
            }
        })
    
    }



    tap(){
        // console.log(this.state.str1)

        if(this.state.str=="mima"){
            this.setState({
                str:"shurukuang",
                str1:"text"
            })
        }else{
            this.setState({//第一次点击不是密码状态,设置密码password状态
               str:"mima",
               str1:"password" 
            })
        }
    }
	
	ff(){
		this.props.history.push('/index/login')
	}

    fanhui(e){
        // this.props.history.push(/)
        this.props.history.go(-1)
    }
    render(){
        return(
            <div>
                
                <div className="div5">
                    <span className="iconfont span1" onClick={this.fanhui.bind(this)}>&#xe646;</span>
                    <span className="span2">注册</span>
                </div>
                <Router>
                    <div>
                       <header className="header2">
                           <span><img src={img}/></span>
                       </header>
                       <div className="div6">
                            <form>
                                <div className="div1-1">
                                     <span className="iconfont font1" id="phone">&#xe768;</span>
                                    <input type="text" placeholder="  请输入手机号" onBlur={this.phone.bind(this)} ref='pho'></input>
                                </div>
                                <div className="div1-1">
                                     <span className="iconfont font1">&#xe65c;</span>
                                    <input type="text" placeholder="  请设置账号" onBlur={this.zhang.bind(this)} ref='word'></input>
                                </div>
                                <div className="div1-1">
                                     <span className="iconfont font1 ">&#xe612;</span>
                                    <input type={this.state.str1} placeholder="  请设置密码" onBlur={this.mi.bind(this)} ref="mima"></input>
                                    <div className="tap iconfont" onClick={this.tap.bind(this)}>&#xe61b;</div>
                                </div>
                                <div className="div6-c">{this.state.str2}</div>
                                <div className="div6-b" onClick={this.regist.bind(this)}>确定</div>
                                
                                <div className="div6-1">
                               		 <span onClick={this.ff.bind(this)}>直接登录</span>
                                    <span>忘记密码?</span>
                                </div>
                            </form>
                            <div className="div6-2">
                                第三方平台登录
                            </div>
                            <div className="div6-3">
                                <figure>
                                    <figcaption className="iconfont fon1">&#xe73e;</figcaption>
                                    <span>
                                        QQ登录
                                    </span>
                                </figure>
                                <figure>
                                    <figcaption className="iconfont fon2">&#xe73b;</figcaption>
                                    <span>
                                        微信登录
                                    </span>
                                </figure>
                                <figure>
                                    <figcaption className="iconfont fon3">&#xe636;</figcaption>
                                    <span>
                                        微博登录
                                    </span>
                                </figure>
                            </div>
                       </div>
                    </div>
                </Router>
            </div>
        )
    }
}

export default Regist;